.home-page {
    height: 100vh;
    background-color: #141414; /* Netflix dark background */
    font-family: Verdana;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  /* Header */
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px;
  }
  
  .header-logo img {
    height: 50px;
  }
  
  .header-right {
    display: flex;
    align-items: center;
  }
  
  .language-selector {
    margin-right: 20px;
    background: transparent;
    border: 1px solid white;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .sign-in-btn {
    font-family: Verdana;
    background-color: red;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
  }
  
  /* Hero Section */
  .hero {
    text-align: center;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero-content {
    max-width: 600px;
  }
  
  .hero-content h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    font-weight: bold;
  }
  
  .hero-content h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .hero-content p {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .hero-input {
    display: flex;
    justify-content: center;
  }
  
  .hero-input input {
    font-family: Verdana;
    padding: 10px;
    font-size: 1rem;
    border: none;
    border-radius: 5px 0 0 5px;
    outline: none;
    width: 300px;
    background-color: #333;
    color: white;
  }
  
  .hero-input button {
    font-family: Verdana;
    background-color: red;
    color: white;
    border: none;
    border-radius: 0 5px 5px 0;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
  }
  
  /* Footer */
  .footer {
    text-align: center;
    padding: 20px;
    font-size: 1rem;
    background-color: black;
    color: white;
  }
  