/* Login Page Styling */
.login-page {
    font-family: Verdana;
    height: 100vh;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; /* To position the logo */
  }
  
  .login-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }
  
  /* Logo Styling */
  .login-logo {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 3;
  }
  
  .login-logo img {
    width: 150px; /* Adjust as needed */
    height: auto;
  }
  
  /* Login Form Styling */
  .login-form {
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 40px;
    border-radius: 10px;
    color: white;
    text-align: center;
    font-size: 10px;
    font-family: Verdana;
  }
  
  .login-form input {
    font-family: Verdana;
    width: 80%;
    padding: 10px;
    margin-bottom: 15px;
    border: none;
    border-radius: 5px;
    background: #333;
    color: white;
  }
  
  .login-button {
    font-family: Verdana;
    background-color: red;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-button:hover {
    background-color: #e50914;
  }
  
  .back-to-home {
    font-family: Verdana;
    margin-top: 20px;
    padding: 10px 20px;
    background: transparent;
    border: 1px solid white;
    color: white;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .back-to-home:hover {
    background: white;
    color: black;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .login-form {
      width: 90%;
      padding: 20px;
    }
  }
  