/* General styles for the search page */
.search-page {
    background-color: #141414;
    color: #fff;
    min-height: 100vh;
    font-family: Verdana;
    padding: 20px;
  }
  
  /* Top menu styles */
  .top-menu {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    background-color: #000;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }
  
  .netflix-logo {
    width: 100px;
    cursor: pointer;
  }
  
  .menu-link {
    color: #fff;
    margin-left: 20px;
    font-size: 1rem;
    background: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
  }
  
  .menu-link:hover {
    text-decoration: underline;
  }
  
  /* Search input styles */
  .search-input {
    margin-top: 80px; /* Leave space for the fixed top menu */
    width: 100%;
    padding: 10px 15px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    background-color: #333;
    color: #fff;
    outline: none;
  }
  
  .search-input::placeholder {
    color: #888;
  }
  
  /* Search results container */
  .search-results {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin-top: 20px;
  }
  
  /* Individual movie item */
  .movie-item {
    width: 150px;
    text-align: center;
    cursor: pointer;
  }
  
  .movie-thumbnail {
    width: 100%;
    border-radius: 5px;
    transition: transform 0.3s;
  }
  
  .movie-thumbnail:hover {
    transform: scale(1.05);
  }
  
  .movie-item p {
    margin-top: 10px;
    font-size: 0.9rem;
    color: #ddd;
  }
  