/* General Styling */
.home-screen-after-login {
    background-color: #141414;
    color: #fff;
    font-family: Verdana;
    min-height: 100vh;
  }
  
  /* Featured Movie Section */
  .featured-movie {
    position: relative;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    overflow: hidden;
  }
  
  .featured-movie-video {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .featured-movie-overlay {
    position: relative;
    z-index: 1;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    border-radius: 10px;
  }
  
  .featured-movie-buttons .play-button,
  .featured-movie-buttons .info-button {
    padding: 10px 20px;
    font-size: 1rem;
    margin-right: 10px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
  }
  
  .play-button {
    background-color: #e50914;
    color: white;
  }
  
  .info-button {
    background-color: #333;
    color: white;
  }
  
  /* Movie Categories */
  .movie-categories {
    padding: 20px;
  }
  
  .category-section {
    margin-bottom: 30px;
  }
  
  .category-title {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .movie-list {
    display: flex;
    gap: 15px;
    overflow-x: auto;
  }
  
  .movie-item {
    flex: 0 0 auto;
    text-align: center;
    width: 150px;
  }
  
  .movie-thumbnail {
    width: 100%;
    height: 225px;
    object-fit: cover;
    border-radius: 5px;
    transition: transform 0.2s ease-in-out;
    cursor: pointer;
  }
  
  .movie-thumbnail:hover {
    transform: scale(1.05);
  }
  
  .movie-title {
    font-size: 0.9rem;
    margin-top: 5px;
  }
  
  /* Modal Styling */
  .movie-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: #222;
    border-radius: 10px;
    padding: 20px;
    width: 90%;
    max-width: 600px;
    color: #fff;
    text-align: center;
  }
  
  .modal-thumbnail {
    width: 100%;
    height: 300px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
  }
  
  .close-button {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 1.5rem;
    color: #fff;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .recommended-section {
    margin-top: 20px;
  }
  
  .recommended-list {
    display: flex;
    gap: 15px;
    overflow-x: auto;
  }
  
  .recommended-item {
    flex: 0 0 auto;
    width: 120px;
    text-align: center;
  }
  
  .recommended-thumbnail {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 5px;
  }

  .top-menu {
    display: flex;
    align-items: center;
    padding: 15px 30px;
    background-color: #141414;
    color: white;
  }
  
  .netflix-logo {
    height: 40px;
    margin-right: 30px;
  }
  
  .menu-links {
    display: flex;
    gap: 20px;
  }
  
  .menu-link {
    font-family: Verdana;
    background: none;
    border: none;
    color: white;
    font-size: 18px;
    cursor: pointer;
  }
  
  .menu-link:hover {
    font-family: Verdana;
    color: #e50914;
    transition: color 0.2s ease-in-out;
  }
  .manager-menu {
    font-family: Verdana;
    position: relative;
  }
  
  .manager-dropdown {
    font-family: Verdana;
    position: absolute;
    top: 30px;
    left: 0;
    background-color: #141414;
    border: 1px solid #333;
    border-radius: 5px;
    padding: 10px;
    z-index: 10;
  }
  
  .dropdown-item {
    color: white;
    background: none;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    text-align: left;
  }
  
  .dropdown-item:hover {
    background-color: #333;
  }
    