/* AddMoviePage.css */

/* General page styling */
.add-movie-page {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
  }
  
  .add-movie-page h1 {
    text-align: center;
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .add-movie-page form {
    display: flex;
    flex-direction: column;
  }
  
  .add-movie-page input,
  .add-movie-page textarea,
  .add-movie-page button {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .add-movie-page textarea {
    resize: none;
    height: 80px;
  }
  
  .add-movie-page input:focus,
  .add-movie-page textarea:focus {
    border-color: #007bff;
    outline: none;
  }
  
  .add-movie-page label {
    font-size: 14px;
    margin-bottom: 5px;
    color: #555;
  }
  
  .add-movie-page button {
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .add-movie-page button:hover {
    background-color: #0056b3;
  }
  
  .add-movie-page .error-message {
    color: red;
    font-size: 14px;
    margin-bottom: 15px;
    text-align: center;
  }
  
  /* File upload fields */
  .add-movie-page input[type='file'] {
    padding: 5px;
  }
  
  .add-movie-page div {
    display: flex;
    flex-direction: column;
  }
  
  /* Responsive styling */
  @media (max-width: 768px) {
    .add-movie-page {
      width: 90%;
      margin: 20px auto;
    }
  
    .add-movie-page h1 {
      font-size: 20px;
    }
  
    .add-movie-page input,
    .add-movie-page textarea,
    .add-movie-page button {
      font-size: 14px;
    }
  }
  