.register-page {
    height: 100vh;
    background-color: #141414;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Verdana;
  }
  
  .register-form {
    font-size: 10px;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 40px;
    border-radius: 10px;
    text-align: center;
    width: 100%;
    max-width: 400px;
  }
  
  .register-form h1 {
    margin-bottom: 20px;
  }
  
  .register-form input {
    font-family: Verdana;
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: none;
    border-radius: 5px;
    background: #333;
    color: white;
  }
  
  .file-input {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .file-input input {
    margin-top: 10px;
    color: white;
  }
  
  .image-preview {
    display: block;
    margin: 15px auto;
    max-width: 100px;
    border-radius: 50%;
    border: 2px solid white;
  }
  
  .register-button {
    font-family: Verdana;
    background-color: red;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
  }
  
  .register-button:hover {
    background-color: #e50914;
  }
  